import "../css/Mainpage.css";
import { useState } from "react";
import { Link } from "react-router-dom";
import Popup from "./Popup"; // nh import Popup v nha
import { useNavigate } from "react-router-dom";

const MainPage = () => {
  const [showPopup, setShowPopup] = useState(false);
  const [isHiding, setIsHiding] = useState(false);
  const Navigate = useNavigate();
  const handleNoClick = () => {
    setShowPopup(true);
    setTimeout(() => {
      setIsHiding(true);
      setTimeout(() => {
        setShowPopup(false);
        setIsHiding(false);
        Navigate(0);
      }, 500); // ng bng thi gian animation fadeOutUp
    }, 2000);
  };

  const handleClosePopup = () => {
    setShowPopup(false);
  };

  return (
    <div className="Container">
      <h1 className="Title">
        {"Hi b iuuu ca anh! Em c nh hm nay l ngy g hemmm nh <33"
          .split(" ")
          .map((word, index) => (
            <span
              key={index}
              style={{
                animationDelay: `${index * 0.08}s`,
                display: "inline-block",
              }}
            >
              {word}&nbsp;
            </span>
          ))}
      </h1>

      <div className="button mt-5">
        <Link to={"/iu"}>
          <button className="yes m-4 btn-animate">Nh n {"<3"}</button>
        </Link>
        <button className="no m-4 btn-animate" onClick={handleNoClick}>
          Ngy g nh??
        </button>
      </div>

      {showPopup && (
        <div className={`popup ${isHiding ? "hide" : "show"}`}>
          <div className="popup-content">
            <span className="popup-icon"></span>
            <h3> ka b hng nh ?</h3>
            <p>Huhu nh li i m!</p>
          </div>
        </div>
      )}
    </div>
  );
};

export default MainPage;
